import React from "react";

import "./header.scss";

const Header = () => {
  const wedDate = new Date(2022, 3, 23);
  const today = new Date()
  const countdown = Math.round((wedDate - today)/(1000*60*60*24),0)

  return (
    <div className="header">
      <div className="image-container-top"></div>
      <h1 className="header__heading">Grant &amp; Jessica</h1>
      <p className="header__tagline">April 23rd, 2022 &#8231; Waxhaw, NC</p>
      <p className="header__countdown">{countdown} days remaining!</p>
      <nav className="nav">
        <ul className="nav__list">
          <li>
            <a href="" className="nav__item">
              Home
            </a>
          </li>
          <li>
            <a href="" className="nav__item">
              Our Story
            </a>
          </li>
          <li>
            <a href="" className="nav__item">
              Venue
            </a>
          </li>
          <li>
            <a href="" className="nav__item">
              Travel
            </a>
          </li>
          <li>
            <a href="" className="nav__item">
              Things to Do
            </a>
          </li>
          <li>
            <a href="" className="nav__item">
              FAQ
            </a>
          </li>
          <li>
            <a href="" className="nav__item">
              RSVP
            </a>
          </li>
        </ul>
      </nav>
    </div>
  );
};

export default Header;
