import React from "react";

import "./home.scss";
import Hero from '../../images/crowders-mtn.jpg'

const Home = () => (
  <div className="home">
      <img src={Hero} alt="Grant and I on top of Crowders Mountain" className="home__hero"></img>
      <div className="home__date-container">
          <p className="home__date-container__date">April 23, 2022</p>
          <p className="home__date-container__location">Waxhaw NC</p>
      </div>
      <div className="flower-container-center"></div>
      <div className="home__details">
            <h2 className="home__details__event">Wedding Day</h2>
            <h3 className="home__details__date">April 23, 2022</h3>
            <p className="home__details__detail">Red Barn Events</p>
            <p className="home__details__detail">6012 Jeanne Dr., Waxhaw, NC, 28173, USA</p>
            <p className="home__details__detail">Outdoor ceremony followed by reception in covered, open-air barn.</p>
            <p className="home__details__detail"><strong>Attire:</strong> Casual Dressy</p>
      </div>
      <div className="home__schedule">
          <p className="home__schedule__time">4:30pm - 5:00pm</p>
          <p className="home__schedule__event">guest arrival</p>
          <p className="home__schedule__time">5:00pm - 5:30pm</p>
          <p className="home__schedule__event">ceremony</p>
          <p className="home__schedule__time">5:30pm - 6:30pm</p>
          <p className="home__schedule__event">cocktail hour</p>
          <p className="home__schedule__time">6:30pm - 10:00pm</p>
          <p className="home__schedule__event">reception</p>
          <p className="home__schedule__time">10:00pm - 10:15pm</p>
          <p className="home__schedule__event">sparkler sendoff</p>
      </div>
  </div>
);

export default Home;